@import url(https://fonts.googleapis.com/css?family=Lato:100,300,400,700&display=swap);
/* Colors.
 ------------------------------------------------- */
/* React Scroll Up Button.
 *
 * Documentation can be found here:
 * https://github.com/dirtyredz/react-scroll-up-button
 ------------------------------------------------- */
.scroll-up-button {
  border-radius: 5px;
  height: 40px !important;
  padding: 10px;
  width: 40px !important; }
  .scroll-up-button:active, .scroll-up-button:focus {
    outline: none; }
  .scroll-up-button:hover {
    background-color: #42C17E !important; }

/* Colors.
 ------------------------------------------------- */
/* Page template.
-------------------------------------------------- */
html {
  min-height: 100%;
  position: relative; }

body {
  color: #252525;
  font-family: Lato, 'Helvetica Neue', Arial, Helvetica, sans-serif;
  font-size: 14px;
  margin: 0; }
  body .segment {
    padding: 8rem 14rem; }

/* Scrollbar.
 ------------------------------------------------- */
::-webkit-scrollbar {
  background-color: #F5F5F5;
  height: 8px;
  width: 8px; }

::-webkit-scrollbar-thumb {
  background-color: #CBCBCB; }

::-webkit-scrollbar-thumb:hover {
  background-color: #BABABA; }

::-webkit-scrollbar-track {
  background-color: #F5F5F5; }

/* Selection.
 ------------------------------------------------- */
::-moz-selection {
  background: #42C17E; }
::selection {
  background: #42C17E; }

/* Colors.
 ------------------------------------------------- */
/* Generic.
-------------------------------------------------- */
a {
  background: 0 0;
  color: #4183C4;
  margin: 0 3px;
  text-decoration: none; }

a:hover {
  text-decoration: underline; }

button {
  background-color: #E0E1E2;
  border: none;
  border-radius: 3px;
  color: #252525;
  font-size: 0.9rem;
  font-weight: 600;
  padding: 10px 20px; }

button:focus {
  outline: 0; }

button:hover {
  background-color: #CACBCD;
  color: rgba(0, 0, 0, 0.8);
  cursor: pointer; }

.big-icon {
  font-size: 1.8rem;
  margin-right: 10px; }

.icon-left {
  margin-right: 5px; }

.icon-right {
  margin-left: 5px; }

h1 {
  font-size: 1.9rem;
  font-weight: 600; }

h2 {
  font-size: 1.6rem;
  font-weight: 600; }

h3 {
  font-size: 1rem;
  font-weight: 600; }

p {
  font-size: 1.2rem;
  line-height: 1.8rem; }

/* Bio component.
-------------------------------------------------- */
.bio {
  display: flex;
  min-height: 300px; }
  .bio .left {
    align-self: center;
    padding-right: 40px;
    width: 60%; }
    .bio .left .section:first-child {
      margin-bottom: 50px; }
    .bio .left .section .title {
      align-items: center;
      display: flex;
      flex-direction: row; }
  .bio .right {
    align-self: center;
    display: flex;
    justify-content: center;
    padding-left: 40px;
    width: 40%; }
    .bio .right .portrait {
      border-radius: 10px;
      width: 100%; }

/* Contact component.
-------------------------------------------------- */
.contact {
  border-top: 1px solid #E0E1E2; }

/* Downloads component.
-------------------------------------------------- */
.downloads {
  align-items: center;
  border-bottom: 1px solid #E0E1E2;
  display: flex;
  flex-direction: column; }
  .downloads a {
    color: #252525;
    -webkit-transition: color 250ms linear;
    transition: color 250ms linear; }
  .downloads a:hover {
    color: #42C17E;
    -webkit-transition: color 250ms linear;
    transition: color 250ms linear; }
  .downloads svg {
    font-size: 4rem; }

/* Footer component.
-------------------------------------------------- */
.footer {
  align-items: center;
  background-color: #252525;
  color: #FFFFFF;
  display: flex;
  justify-content: space-between;
  padding: 30px 20px; }
  .footer .logo {
    height: 30px; }

/* Header component.
-------------------------------------------------- */
.header .hero-content {
  display: flex;
  flex-direction: column;
  position: relative; }
  .header .hero-content .banner {
    height: 50%; }
  .header .hero-content .sub-text {
    color: #FFFFFF;
    font-size: 1.6rem; }

/* Portfolio component.
-------------------------------------------------- */
.portfolio .cards {
  display: flex;
  flex-wrap: wrap;
  justify-content: center; }
  .portfolio .cards .portfolio-card {
    border: 1px solid #E0E1E2;
    border-radius: 5px;
    display: flex;
    flex-direction: column;
    margin: 10px;
    max-width: 300px;
    overflow: hidden;
    width: calc((100% / 3) - 25px); }
    .portfolio .cards .portfolio-card .image {
      border-bottom: 1px solid #E0E1E2; }
      .portfolio .cards .portfolio-card .image img {
        width: 100%; }
    .portfolio .cards .portfolio-card .content {
      border-bottom: 1px solid #E0E1E2;
      flex-grow: 1;
      padding: 15px; }
      .portfolio .cards .portfolio-card .content:last-child {
        border-bottom: none; }
      .portfolio .cards .portfolio-card .content.extra {
        flex-grow: 0; }
      .portfolio .cards .portfolio-card .content .title {
        font-size: 1.2rem;
        font-weight: 600;
        margin-bottom: 5px; }
      .portfolio .cards .portfolio-card .content .sub-title {
        color: #ACACAC;
        margin-bottom: 10px; }
      .portfolio .cards .portfolio-card .content .paragraph {
        color: #5C5C5C; }
      .portfolio .cards .portfolio-card .content .date {
        color: #ACACAC; }

/* Social component.
-------------------------------------------------- */
.social {
  display: flex;
  flex-direction: row;
  width: 100%; }
  .social .item {
    align-items: center;
    border: 1px solid #E0E1E2;
    border-left: none;
    display: flex;
    flex-direction: column;
    flex-grow: 1;
    justify-content: center;
    padding-bottom: 50px;
    padding-top: 50px; }
    .social .item:last-child {
      border-right: none; }
    .social .item .top {
      align-items: center;
      display: flex; }

